/* Background for the transparensii-container */
.transparensii-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.video-container video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

/* Styling for the content section */
.content {
  display: flex;
  flex-direction: column; /* Change flex-direction to column to stack elements */
  max-width: 800px; /* Adjust as needed */
  color: #fff; /* Text color for the content */
  padding: 20px; /* Add padding around the content */
  margin-top: -100px; /* Adjust as needed */
}

.content h1 {
  flex :1;
  font-size: 4rem;
  margin-top: 300px;
  margin-left: -200px; /* Reduce margin to bring the sentence closer */
}

.content p {
  flex :1;
  font-size: 1.5rem; /* Adjust font size for the sentence */
  margin-top: 20px;
  margin-left: -200px
}

.content video {
  flex :1;
  position: relative;
  width: 110%;
  height: 110%;
  object-fit: contain;
  transform-box: fill-box;
  transform-origin: center;
  transform: scale(1);
  transition: transform 0.5s ease-in-out; /* Add easing for a smoother transition */
  border-radius: 5px;
  margin-left: 370px;
  margin-top: -220px;
}

.content h2 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.content p:not(:last-child) {
  margin-bottom: 20px;
}
